$fonts1: 'Montserrat', sans-serif;

$green: #00c180;

body.logged-in.admin-bar #mainNav {
    margin-top: 32px;
}

@media screen and (max-width: 782px) {
    body.logged-in.admin-bar #mainNav {
        margin-top: 46px;
    }
}
body {
	margin: 0;
	font-family: 'Noto Sans', sans-serif;
}
h1, h2, h3, h4, h5 {
	font-family: $fonts1;

	a {
		color: #000;
	}
}
#topBar {
    padding: 5px 0;
    background: #000;
}
.topSM {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    color: #fff;

    .title {
	    margin-right: 15px;
	}
}
.socialMedia {
	img {
	    height: 20px;
	}
}
nav.navbar {
    padding: 15px 0;

    .logo img {
		width: 100px;
		height: auto;
	}
}

#main-nav {
	a.nav-link {
	    color: #000;
	    font-weight: bold;
	    text-transform: uppercase;
	    padding: 8px 20px;
	}
	a.nav-link:hover {
		color: $green;
	}
}


footer {
    background: #000;
    color: #fff;
    padding: 50px 0 0;

    .container {
    	display: flex;
	    flex-wrap: wrap;
        justify-content: space-between;
    }

    .logo img {
    	width: 100px;
    	height: auto;
    }
    h3 {
    	font-weight: normal;
    }
    h3::after {
	    width: 100px;
	    height: 2px;
	    content: '';
	    display: block;
	    border-bottom: 2px solid $green;
        margin: 5px 0;
	}
	.nav-link {
	    display: inline-block;
        color: #fff;
    	padding: 0px 10px;
	}
	.socialMedia {
	    margin-top: 15px;
	}
}
.fw-image {
    /* position: relative; */
    width: 100%;
    height: 0;
    padding-bottom: 35%;
    background: url('/wp-content/themes/leesa/images/multi-media-zappital.png');
    background-size: cover;
    background-position: center;
}
.main-title {
    padding: 9% 0 25px 49%;
}
h1 {
    font-weight: bold;
}
.subH1 {
    font-size: 30px;
    line-height: 30px;
    font-weight: bold;
    font-family: $fonts1;
}
.main-title h1 {
    margin-bottom: 0;
}
h2::after {
    width: 100px;
    height: 2px;
    content: '';
    display: block;
    border-bottom: 2px solid $green;
    margin: 10px 0;
}
h2 {
    margin-top: 40px;
    font-weight: bold;
}
h3 {
    font-weight: bold;
    font-size: 1.5em;
}
h4 {
    font-size: 1em;
    text-transform: uppercase;
    font-weight: bold;
    border-bottom: 1px solid $green;
}
.sectionTitle {
    text-align: center;

    h2::after {
    	margin: 10px auto;
    }
}
#services .body {
    display: flex;
    flex-wrap: wrap;
    margin: 50px 0;
    justify-content: space-between;
}
.entry {
	width: calc(33.33% - 15px);
    display: flex;

    .text {
	    margin-left: 15px;
        width: calc(100% - 65px);
	}
}
.greenBubble {
    width: 50px;
    height: 50px;
    background: linear-gradient(#75b1b2, #4f7e74);
    border-radius: 50%;
    border: 1px solid #639699;
    font-size: 25px;
    line-height: 49px;
    text-align: center;
}
i.white::before {
    color: white;
}
#leverage {
	background: #bbc3db2b;
    background-image: linear-gradient( to left, transparent 1%, #e9ecef 67% ), url(/wp-content/themes/leesa/images/pen-plants.jpg);
    background-size: cover;
    background-position: top;
	padding: 10px 0 50px;

	.body {
		padding: 0 90px;
	}
}
.col1 {
    display: flex;
    flex-wrap: wrap;
}   
.socialMedia a {
	margin-left: 5px;
}
.copyright {
    width: 100%;
    padding: 15px 0;
    border-top: 1px solid #f5f5f50f;
    margin-top: 15px;
}
span.greenText {
	color: $green;
}
.qLinks {
	h3 {
		margin-bottom: 25px;
	}
	

	ul {
	    column-count: 3;
	    list-style: none;
	    padding-left: 0;

	    li::before {
		    content: '>';
		    display: inline-block;
		}
	}	
}
#joinNewsletter {
    margin-bottom: 50px;

    .body {
	    max-width: 500px;
        margin: 0 auto;
    	text-align: center;
    }

    input {
        border-radius: 50px 0 0 50px;
	    border: 0;
	    box-shadow: 0 0 7px #dfdcd9;
	    line-height: 38px;
        padding: 0 0 0 15px;
	    width: 230px;
	}
	.btn-green {
		border-radius: 0 50px 50px 0;
	    display: inline-block;
    	vertical-align: top;
	}
	form.form-inline {
	    justify-content: center;
	}
}
.btn-green {
    background: linear-gradient(#75b1b2, #4f7e74);
    color: #fff;
    border-radius: 50px;
    line-height: 40px;
    padding: 0 15px;
    border: 0;
}
.btn-green:hover {
    background: linear-gradient(#4f7e74, #75b1b2);
    color: #fff;
}
.fw-page .fw-image {
    padding: 1px 0 0;
    height: 145px;
    background-position: top;
    margin-bottom: 50px;
}
.postsSidebar {
    width: 300px;
}
.postsCol {
    width: calc(100% - 315px);
}
.blog .fw-page .container {
    display: flex;
    justify-content: space-between;
}
.postsCol h2 {
    margin-top: 0;
    margin-bottom: 25px;
}
.postShort {
    border-bottom: .5px solid black;
    margin-bottom: 40px;
    padding-bottom: 15px;
}
.date {
    font-style: italic;
    font-size: 0.9em;
    margin-bottom: 10px;
}
#mailmunch-optin-form {
    border: 0 !important;
    max-width: 500px;
    margin: 0 auto;

    h1 {
		margin-top: 40px;
		font-size: 32px;
		font-weight: bold;
	}

	h1::after {
	    width: 100px;
	    height: 2px;
	    content: '';
	    display: block;
	    border-bottom: 2px solid #00c180;
	    margin: 10px auto;
	}
}
.btn-cont {
    display: flex;
    justify-content: center;
}
@media (max-width: 1199px) {
   .main-title {
        padding: 7% 0 25px 45%;

        h1 {
            font-size: 2.25em;
        }
    }
    .subH1 {
        font-size: 25px;
        line-height: 25px;
    } 
}
@media (max-width: 991px) {
   .main-title {
        
        h1 {
            font-size: 1.75em;
        }
    }
    .subH1 {
        font-size: 20px;
        line-height: 20px;
    }
    .entry {
        width: calc(50% - 15px);
        flex-wrap: wrap;
        justify-content: center;
        text-align: center;
    }
    .entry .text {
        margin-left: 0px;
        margin-top: 10px;
        width: 100%;
        min-height: 150px;
    }
    #services .body {
        margin: 20px 0;
    } 
}
@media (max-width: 767px) {
    body {
        font-size: 14px;
    }
    h2 {
        font-size: 1.75em;
        margin-top: 20px;
    }
    h3 {
        font-size: 1.25em;
        line-height: 20px;
    }
    .main-title {
        padding: 7% 0 25px 45%;

        h1 {
            font-size: 19px;
            line-height: 20px;
        }
    }
    .subH1 {
        font-size: 14px;
        line-height: 16px;
    }
    .entry .text {
        min-height: unset;
    }
    .greenBubble {
        width: 40px;
        height: 40px;
        font-size: 20px;
        line-height: 40px;
    }
    .fw-image {
        padding-bottom: 40%;
    }
    #leverage {
        background: #bbc3db2b;
        background-image: linear-gradient(to bottom, transparent 1%, #e9ecef 60%), url(/wp-content/themes/leesa/images/pen-plants.jpg);
        background-size: cover;
        background-position: top;
        padding: 10px 0 20px;
    }
    .navbar-nav {
        margin-top: 15px;
        text-align: right;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: flex-end;
    }
    #main-nav a.nav-link {
        padding: 8px 10px;
        display: inline-block;
    }
}
@media (max-width: 525px) {
    .entry {
        width: 100%;
        max-width: unset;
        flex-wrap: wrap;
        justify-content: center;
        text-align: center;
    }
    .entry .text {
        min-height: unset;
    }
    #services .body {
        justify-content: center;
    }
    #leverage {
        background: #bbc3db2b;
        background-image: linear-gradient(to bottom, transparent 1%, #e9ecef 60%), url(/wp-content/themes/leesa/images/pen-plants.jpg);
        background-size: cover;
        background-position: top;
        padding: 10px 0 20px;
    }
    #leverage .body {
        padding: 0 0px;
    }
    footer {
        padding: 20px 0 0;
    }
    .qLinks {
        margin-top: 10px;

        ul {
            column-count: 2;
        }
    }
    .socialMedia a {
        margin-left: 0;
    }
    .main-title {
        padding: 6% 25px 25px 15px;
        text-align: right;
    }
    .fw-image {
        padding-bottom: 70%;
    }
}